import { Controller, Block, Commands } from '@ali/4ever-cangjie';
import { CodeData } from '../types';

export default function updateCode(
  controller: Controller,
  node: Block,
  data: Partial<CodeData>,
) {
  return controller.command(Commands.setNodeByKey, node.key, {
    type: 'code',
    data: {
      ...node.data,
      ...data,
    },
  });
}
